<template>
  <div>
    <div v-for="job in jobdetail" :key="job.id">
      <v-img src="https://bizlx.itechvision.in/images/banner1.jpg" cover
             class="white--text align-center"
             height="250" gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)">
        <v-card-title class="justify-center h2">{{ job.name }}</v-card-title>
      </v-img>
      <v-container>
        <div class="text-center">
          <v-btn color="cred" class="me-2 my-1" @click="goBack"><v-icon>mdi-chevron-left</v-icon>Back</v-btn>
          <a href="#" class="me-2"><v-btn color="info" class="my-1">
            <v-icon>mdi-heart-outline</v-icon>Add to Wishlist</v-btn></a>
          <a href="#" class="me-2"><v-btn color="success" class="my-1">
            <v-icon>mdi-map-marker</v-icon>Google Location</v-btn></a>
          <router-link :to="{name: 'jobsbycity', params: { business: job.username }}" class="me-2">
            <v-btn color="info" class="my-2">View All Jobs</v-btn>
          </router-link>
        </div>
        <v-row>
          <v-col cols="12" md="8" class="order-1 order-md-2">
            <div class="accordion py-2" role="tablist">
              <b-card no-body class="mb-1">
                <b-card-header header-tag="header" class="p-1" role="tab">
                  <b-button class="w-100 catsy fw-bold cblu d-flex justify-content-between btn-sm"
                            v-b-toggle.accordion-1 variant="light">Description</b-button>
                </b-card-header>
                <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
                  <b-card-body>
                    <b-card-text>
                      {{ job.job_description }}
                    </b-card-text>
                  </b-card-body>
                </b-collapse>
              </b-card>
            </div>
          </v-col>
          <v-col cols="12" md="4" class="order-2 order-md-1">
            <div class="apply-job shadow border">
              <h2 class="h5 cred mb-0">{{ job.job_title }}</h2>
              <div>{{ job.name }}</div>
              <div class="mb-2">{{ job.city }}, {{ job.state }}</div>
              <h2 class="h5 cred">Job Details</h2>
              <h3 class="h5">Salary</h3>
              <ul class="list-unstyled mb-5 ps-2">
                <li><v-icon class="cred" size="20">mdi-check-bold</v-icon>â‚¹{{ job.salary_from }} a Month</li>
              </ul>
              <h3 class="h5">Job Type</h3>
              <ul class="list-unstyled mb-5 ps-2">
                <li><v-icon class="cred" size="20">mdi-check-bold</v-icon>{{ job.job_type }}</li>
              </ul>
              <h3 class="h5">Qualifications</h3>
              <ul class="list-unstyled mb-5 ps-2">
                <li><v-icon class="cred" size="20">mdi-check-bold</v-icon>
                  {{ job.qualification }}</li>
              </ul>
              <h3 class="h5">Experience</h3>
              <ul class="list-unstyled mb-5 ps-2">
                <li><v-icon class="cred" size="20">mdi-check-bold</v-icon>
                  <span v-if="job.min_exp == 0">Fresher</span>
                  <span v-else>{{ job.min_exp }} Years</span>
                </li>
              </ul>
              <a v-b-modal.applynow href="#" ><v-btn color="success">Apply Now</v-btn></a>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <b-modal id="applynow" title="Apply Now" hide-footer class="w-25">
      <form ref="form" @submit.stop.prevent="" class="job-page-form">
        <div class="row">
          <div class="col-6">
              <div class="">
                  <b-form-input type="text" id="name" required placeholder="Name"></b-form-input>
              </div>
          </div>
          <div class="col-6">
            <div class="">
              <b-form-input type="email" id="email"  required placeholder="Email"></b-form-input>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <div class="">
              <b-form-input type="number" id="phone"  required placeholder="Phone No"></b-form-input>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <div class="">
              <select class="form-select" id="Qualification">
                <option selected disabled>--Qualification--</option>
                <option value="1">10Th</option>
                <option value="2">+2</option>
                <option value="3">BA</option>
              </select>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <div class="">
              <select class="form-select" id="Experience">
                <option selected disabled>--Experience--</option>
                <option value="1">Fresher </option>
                <option value="2">6 Month - 1 Year</option>
                <option value="3">1 Year - 2 Years</option>
              </select>
            </div>
          </div>
        </div>
        <div class="mt-3">
            <b-button variant="success" type="submit">Submit</b-button>
        </div>
      </form>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "JobDetail",
  props: {
    id: Number,
  },
  data(){
    return{
      file2: null,
      jobdetail:[],
    }
  },
  mounted() {
    this.getjobdetail();
  },
  methods: {
    goBack() {
      window.history.back();
    },
    getjobdetail(){
      axios.get(`jobcategory/job-detail/${this.id}`)
          .then((resp) =>{
            console.log(resp.data)
            this.jobdetail = resp.data.jobdetails;
          })
    }
  }
}
</script>

<style scoped>
.apply-job {
  background: #e5e5e5;
  padding: 10px;
}

input#file {
  background: none !important;
  }
form.job-page-form select#Qualification {
  background-color: #f2f2f2;
  }
form.job-page-form select#Experience {
  background-color: #f2f2f2;
  }
form.job-page-form input {
  background-color: #f2f2f2 !important;
  }
.btn.catsy.collapsed::after {
    content: '+';
    color: var(--cblu);
  }
  .btn.catsy.not-collapsed::after {
    content: '-';
    color: var(--cblu);
  }
@media(max-width: 576px){
  .v-btn:not(.v-btn--round).v-size--default {
	height: 30px;
	min-width: 64px;
	padding: 0 7px;
}
}

</style>